import Vue from "vue";
import { Language, LanguageToToggle } from "./helpers/types";

export const bus = new Vue();

export function emitToggleLanguage(languageToToggle: LanguageToToggle) {
  bus.$emit("toggleLanguageOnChart", languageToToggle);
}

export function emitChangeBackground(backgroudIndex: number) {
  bus.$emit("changeBackground", backgroudIndex);
}

export function addChangeBackgroundListener(
  listener: (backgroudIndex: number) => void
) {
  bus.$on("changeBackground", listener);
}

export function addToggleLanguageListener(
  listener: (langToToggle: LanguageToToggle) => void
) {
  bus.$on("toggleLanguageOnChart", listener);
}

export function emitExportRequest(format: "png" | "jpeg") {
  bus.$emit("exportRequest", format);
}

export function addExportRequestListener(
  listener: (format: "png" | "jpeg") => void
) {
  bus.$on("exportRequest", listener);
}

export function emitCopyRequest() {
  bus.$emit("copyRequest");
}
export function addCopyRequestListener(listener: () => void) {
  bus.$on("copyRequest", listener);
}
