import axios, { AxiosRequestConfig, AxiosResponse } from "axios";
import { githubAccessToken } from "../../github-api-token";
import { AxiosMethod, UserData } from "./types";

const githubGraphQlApiUrl = "https://api.github.com/graphql";

async function apiRequest(method: AxiosMethod, query: string, variables?: any) {
  const accessToken: string = githubAccessToken;

  try {
    const options: AxiosRequestConfig = {
      method,
      url: githubGraphQlApiUrl,
      data: { query, variables },
      headers: {
        authorization: `Bearer ${accessToken}`
      }
    };

    const resp = await axios(options);

    if (!resp) throw new Error("Error from API: " + resp);

    return resp;
  } catch (err) {
    const resp = err.response;
    if (!resp) throw err;
    if (typeof resp.data === "string")
      throw new Error(
        `HTTP Status ${resp.status} (${resp.statusText}), ${resp.data}`
      );
    throw err;
  }
}

export async function getUserData(username: string): Promise<UserData> {
  const query = `
	query($username: String!) {
		user(login: $username) {
      login
			name
			email
      avatarUrl
      bio
			bioHTML 
			followers {totalCount}
			repositories(first: 100) {
				totalCount 
				nodes {
					description
					languages(first: 100) {
						 nodes { id color name }
					}
				}
			}
		}
	}`;

  const result: AxiosResponse = await apiRequest("POST", query, { username });

  if (result && result.status === 200) {
    if (result.data.data.user) return result.data.data.user as UserData;
    throw new Error(
      "No user associated to the provided username have been found"
    );
  } else {
    throw new Error(
      "An error occured while retrieving user data ! Please try again"
    );
  }
}
