
























































































































// @ is an alias to /src
import PictureGenerator from "@/components/PictureGenerator.vue";
import NavBar from "@/components/NavBar.vue";
import Settings from "@/components/Settings.vue";
import {
  defineComponent,
  onMounted,
  reactive,
  watch
} from "@vue/composition-api";
import { getUserData } from "../helpers/api";
import { LanguageToToggle, UserData } from "@/helpers/types";
import { emitExportRequest, emitCopyRequest } from "../event-bus";

export default defineComponent({
  name: "Home",
  props: {
    userName: {
      type: String,
      required: false
    }
  },
  components: {
    NavBar,
    Settings,
    PictureGenerator
  },
  setup(props, context) {
    const state = reactive({
      username: "",
      isLoadingData: false,
      isExporting: false,
      isCopying: false,
      userData: undefined as UserData | undefined,
      activeBackground:
        "https://image.freepik.com/free-vector/abstract-halftone-background_23-2148598686.jpg",
      languageToToggle: undefined as undefined | LanguageToToggle,
      latestErrorMessage: ""
    });

    async function loadUserData() {
      try {
        state.isLoadingData = true;
        const userData = await getUserData(state.username);
        state.userData = userData;
        state.isLoadingData = false;
      } catch (error) {
        context.root.$swal.fire({
          title: "An error occured !",
          text: error.message,
          icon: "error",
          confirmButtonText: "OK"
        });
        console.log(error.message);
        state.isLoadingData = false;
      }
    }

    function onToggleLanguageOnChart(languageToToggle: LanguageToToggle) {
      state.languageToToggle = languageToToggle;
    }

    function onBackgroundChangeRequest(activeBackground: string) {
      state.activeBackground = activeBackground;
    }

    onMounted(() => {
      if (props && props.userName !== undefined) {
        state.username = props.userName;
        loadUserData();
      }
    });

    watch(
      () => props.userName,
      (userName: string | undefined) => {
        if (userName) {
          state.username = userName;
          loadUserData();
        }
      }
    );

    return {
      state,
      loadUserData,
      onBackgroundChangeRequest,
      onToggleLanguageOnChart,
      emitExportRequest,
      emitCopyRequest
    };
  }
});
