
































































import { Language, LanguageToToggle, UserData } from "@/helpers/types";
import { emitChangeBackground, emitToggleLanguage } from "@/event-bus";
import {
  defineComponent,
  onMounted,
  PropType,
  reactive,
  watch
} from "@vue/composition-api";

export default defineComponent({
  name: "Settings",
  props: {
    userData: {
      type: Object as PropType<UserData>,
      required: true
    }
  },
  setup(props, context) {
    const state = reactive({
      activeBackground: 1,
      languages: [] as Language[],
      uncheckedLanguages: [] as Language[]
    });

    function getLanguages() {
      const userData = props.userData;
      if (userData === undefined) return;

      userData.repositories.nodes.forEach(repo =>
        repo.languages.nodes.forEach(lang => {
          if (!state.languages.find(language => lang.id === language.id))
            state.languages.push(lang);
        })
      );
    }

    function toggleLanguage(lang: Language) {
      const isUncheckedLanguage =
        state.uncheckedLanguages.find(language => language.id === lang.id) !==
        undefined;

      if (isUncheckedLanguage) {
        //Language will be checked
        state.uncheckedLanguages.splice(
          state.uncheckedLanguages.findIndex(
            language => language.id === lang.id
          ),
          1
        );
      } else {
        state.uncheckedLanguages.push(lang);
      }

      emitToggleLanguage({
        language: lang,
        action: isUncheckedLanguage ? "add" : "remove"
      } as LanguageToToggle);

      // context.emit("toggleLanguageOnChart", {
      //   language: lang,
      //   action: isUncheckedLanguage ? "add" : "remove"
      // } as LanguageToToggle);
    }

    function changeBackground(backgroudIndex: number) {
      state.activeBackground = backgroudIndex;
      emitChangeBackground(backgroudIndex);
    }

    onMounted(() => {
      if (props.userData) getLanguages();
    });

    watch(
      () => props.userData,
      () => getLanguages()
    );

    return {
      state,
      changeBackground,
      toggleLanguage
    };
  }
});
