






































































/* eslint-disable @typescript-eslint/no-non-null-assertion */
import {
  defineComponent,
  reactive,
  onMounted,
  watch,
  PropType,
  computed
} from "@vue/composition-api";
import Vue from "vue";
import VueC3 from "vue-c3";
import { Language, LanguageToToggle, UserData } from "../helpers/types";
import {
  addChangeBackgroundListener,
  addExportRequestListener,
  addToggleLanguageListener,
  addCopyRequestListener
} from "@/event-bus";
import domToImage from "dom-to-image-more";
import { copyBlobToClipboard } from "../helpers/clipboard";

export default defineComponent({
  name: "PictureGenerator",
  components: {
    VueC3
  },
  props: {
    userData: {
      type: Object as PropType<UserData>,
      required: true
    },
    languageToToggle: {
      type: Object as PropType<LanguageToToggle>,
      required: false
    }
  },
  setup(props, context) {
    const state = reactive({
      handler: new Vue(),
      activeBackground: 1,
      backgroundUrl: computed(
        (): string => `url('/bg-images/${state.activeBackground}.jpg')`
      ),
      languagesAndAction: [] as LanguageToToggle[]
    });

    interface LanguageAndCount extends Language {
      count: number;
    }

    function drawUserData() {
      const userData = props.userData;
      if (userData === undefined) return;

      const langAndCountMap = new Map<string, LanguageAndCount>();

      userData.repositories.nodes.forEach(repo =>
        repo.languages.nodes.forEach(lang => {
          if (langAndCountMap.has(lang.id)) {
            const count = langAndCountMap.get(lang.id)!.count + 1;
            langAndCountMap.set(lang.id, { ...lang, count });
          } else {
            if (state.languagesAndAction.length > 0) {
              const thisLangAction = state.languagesAndAction.find(
                langAction => langAction.language.id === lang.id
              );

              if (thisLangAction) {
                if (thisLangAction.action === "add")
                  langAndCountMap.set(lang.id, { ...lang, count: 1 });
              } else {
                state.languagesAndAction.push({
                  language: lang,
                  action: "add"
                });
                langAndCountMap.set(lang.id, { ...lang, count: 1 });
              }
            } else {
              state.languagesAndAction.push({ language: lang, action: "add" });
              langAndCountMap.set(lang.id, { ...lang, count: 1 });
            }
          }
        })
      );

      const columns: [string, number][] = [];
      const colors: Record<string, string | null> = {};

      langAndCountMap.forEach(langAndCount => {
        columns.push([langAndCount.name, langAndCount.count]);
        colors[langAndCount.name] = langAndCount.color;
      });

      const options = {
        data: {
          columns,
          colors,
          type: "pie"
        }
      };
      state.handler.$emit("init", options);
    }

    function updateLanguageAndAction(
      languageToToggle: LanguageToToggle | undefined
    ) {
      if (languageToToggle === undefined) return;
      const lang = state.languagesAndAction.find(
        lang => lang.language.id === languageToToggle?.language.id
      );

      if (lang && languageToToggle?.action !== lang.action) {
        const langIndex = state.languagesAndAction.findIndex(
          lang => lang.language.id === languageToToggle?.language.id
        );
        lang.action = languageToToggle?.action as "add" | "remove";

        state.languagesAndAction[langIndex] = lang;
      } else {
        state.languagesAndAction.push(languageToToggle!);
      }

      drawUserData();
    }

    async function exportToImage(format: "png" | "jpeg") {
      context.emit("exportStarted");

      const node = document.getElementById("result-picture");

      if (!node) return;

      const options = { bgcolor: "#FFFFFF" };

      const resultPicture =
        format === "png"
          ? await domToImage.toPng(node, options)
          : await domToImage.toJpeg(node, options);

      // const img = new Image();
      // img.src = resultPicture;
      // document.body.appendChild(img);

      const link = document.createElement("a");
      link.download = `${props.userData.login}-github-profile.${format}`;
      link.href = resultPicture;
      link.click();
      context.emit("exportFinished");
    }

    async function copyToClipboard() {
      context.emit("copyStarted");

      try {
        const node = document.getElementById("result-picture");

        if (!node) return;

        const options = { bgcolor: "#FFFFFF" };

        const resultPicture = await domToImage.toBlob(node, options);

        const dataToWrite = [
          // eslint-disable-next-line no-undef
          new ClipboardItem({ [resultPicture.type]: resultPicture })
        ];

        // await navigator.clipboard.write(dataToWrite);

        await copyBlobToClipboard(dataToWrite);

        context.root.$swal.fire({
          title: "Done !",
          text: "Image successfully copied to clipboard",
          icon: "success",
          confirmButtonText: "OK"
        });
      } finally {
        context.emit("copyFinished");
      }
    }

    onMounted(() => {
      addChangeBackgroundListener(
        bgIndex => (state.activeBackground = bgIndex)
      );
      addToggleLanguageListener(updateLanguageAndAction);
      addExportRequestListener(exportToImage);
      addCopyRequestListener(copyToClipboard);
      if (props.userData) drawUserData();
    });

    watch(
      () => props.userData,
      () => drawUserData()
    );

    watch(
      () => props.languageToToggle,
      () => updateLanguageAndAction(props.languageToToggle)
    );

    return {
      state
    };
  }
});
